.moviedetail {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;

  figure {
    margin: 0;
    padding: 0;

    img {
      max-width: 100%;
    }
  }
}
